<template>
  <div class="login">
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="username"
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"
      />
      <van-field
        v-model="phone"
        type="number"
        name="phone"
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"
      />
      <van-field
        v-model="email"
        type="email"
        name="email"
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"
      />
      <!-- <van-radio-group  style="display:flex" v-model="gender">
        <van-radio name="male"></van-radio>
        <van-radio name="female"></van-radio>
     </van-radio-group> -->
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">
          
        </van-button>
      </div>
