import Vue from 'vue'
import { Form, Field, Button,RadioGroup, Radio  } from 'vant'
import { mapActions } from 'vuex'

Vue.use(Form)
Vue.use(Field)
Vue.use(Button)
Vue.use(Radio);
Vue.use(RadioGroup);
export default {
  name: 'Register',
  data () {
    return {
      username: 'admin',
      password: 'admin',
      email:'admin@foo.com',
      phone:'123456',
      gender:'male'
    }
  },
  created () {
    console.log(this.$store)
  },
  methods: {
    ...mapActions(['loginAsync','registAsync']),
    onSubmit (values) {
      //  vuex  store  action 
      // this.$store.dispatch('loginAsync', values)
      //  mapActions  loginAsync 
      this.registAsync(values)
    }
  }
}
</script>

